import React, {useEffect, useState} from 'react';
import { CButton, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react';

import './ehrCategory.scss';

import CIcon from '@coreui/icons-react'
import {
  cilTrash,
} from '@coreui/icons'
import { Link } from 'react-router-dom';



const EhrCategory=()=> {
  const [patient, setPatient] = useState([])

  useEffect(() => {
    fetch("http://13.233.39.128:5000/api/report-category/list", {
        method: 'POST', // or 'PUT'
        headers: {
            'Content-Type': 'application/json',
        }
    }).then(e=>e.json()).then((data)=>{
        console.log(data);
        setPatient(data.data);
    })
  }, []);

  const deletePatient = (arg)=>{
    window.confirm("Are you Sure you want to delete this");
  }
  

  return (<>
        <div className='d-flex justify-content-between align-items-center mb-3'>
            <h2 className='mb-3'>Report Category</h2>
            <Link className='btn btn-lg btn-primary' to={"/ehr/category/add"}>Add Category</Link>
        </div>
 
    <div className='arTableWrapper'>
        <CTable>
            <CTableHead>
                <CTableRow>
                <CTableHeaderCell width={220} scope="col">Category Id</CTableHeaderCell>
                <CTableHeaderCell scope="col">name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Short Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Description</CTableHeaderCell>
                <CTableHeaderCell width={240} scope="col">Parent</CTableHeaderCell>
                <CTableHeaderCell width={240} scope="col">Status</CTableHeaderCell>
                <CTableHeaderCell width={60} scope="col">Action</CTableHeaderCell>
                </CTableRow>
            </CTableHead>
            <CTableBody>
                {patient.map((i, k)=>(
                    <CTableRow key={k}>
                        <CTableHeaderCell scope="row"><Link to={"/patients/"+i._id}>{i._id}</Link></CTableHeaderCell>
                        <CTableDataCell>{i.name}</CTableDataCell>
                        <CTableDataCell>{i.short_name}</CTableDataCell>
                        <CTableDataCell>{i.description}</CTableDataCell>
                        <CTableDataCell>{i.parent?i.parent:"-"}</CTableDataCell>   
                        <CTableDataCell>{i.status?<CButton color='success' size='sm'>Active</CButton>:<CButton color='warning' size='sm'>Inactive</CButton>} </CTableDataCell>
                        <CTableDataCell className='text-center'> <div onClick={()=>{deletePatient(i._id)}} className='deleteBtnAr'><CIcon icon={cilTrash} /></div></CTableDataCell>                        
                    </CTableRow>
                ))}                
            </CTableBody>
        </CTable>
    </div>
    </>)
}
export default EhrCategory;